import React, { useEffect, useState } from "react";
import { MDXProvider } from "@mdx-js/react";
import Prism from "prismjs";

// Prism.js 테마와 언어 지원 추가
import "../styles/prism-custom.css"; // 커스텀 Gatsby 스타일 테마
import "prismjs/components/prism-javascript";
import "prismjs/components/prism-typescript";
import "prismjs/components/prism-jsx";
import "prismjs/components/prism-tsx";
import "prismjs/components/prism-css";
import "prismjs/components/prism-bash";
import "prismjs/components/prism-json";
import "prismjs/components/prism-java";

// 언어별 색상 맵핑
const getLanguageInfo = (language) => {
  const languageMap = {
    JavaScript: {
      color: "bg-yellow-100 text-yellow-800 border-yellow-200",
      name: "JavaScript",
    },
    TypeScript: {
      color: "bg-blue-100 text-blue-800 border-blue-200",
      name: "TypeScript",
    },
    JSX: {
      color: "bg-yellow-100 text-yellow-800 border-yellow-200",
      name: "JSX",
    },
    TSX: {
      color: "bg-blue-100 text-blue-800 border-blue-200",
      name: "TSX",
    },
    CSS: {
      color: "bg-blue-100 text-blue-800 border-blue-200",
      name: "CSS",
    },
    Shell: {
      color: "bg-gray-100 text-gray-800 border-gray-200",
      name: "Shell",
    },
    JSON: {
      color: "bg-yellow-100 text-yellow-800 border-yellow-200",
      name: "JSON",
    },
    HTML: {
      color: "bg-orange-100 text-orange-800 border-orange-200",
      name: "HTML",
    },
    Java: {
      color: "bg-red-100 text-red-800 border-red-200",
      name: "Java",
    },
  };

  return (
    languageMap[language] || {
      color: "bg-gray-100 text-gray-800 border-gray-200",
      name: language || "text",
    }
  );
};

// 복사 버튼 컴포넌트
const CopyButton = ({ code }) => {
  const [copied, setCopied] = useState(false);

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(code);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("복사에 실패했습니다:", err);
    }
  };

  return (
    <button
      onClick={handleCopy}
      className="group relative px-3 py-1.5 rounded-md text-xs font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-violet-500/20 active:scale-95"
      title={copied ? "복사 완료!" : "코드 복사"}
      style={{
        backgroundColor: copied ? "#8b5cf6 !important" : "#ffffff !important",
        color: copied ? "#ffffff !important" : "#6b7280 !important",
        border: copied
          ? "1px solid #8b5cf6 !important"
          : "1px solid #d1d5db !important",
        boxShadow: copied
          ? "0 2px 4px rgba(139, 92, 246, 0.2)"
          : "0 1px 2px rgba(0, 0, 0, 0.05)",
        fontWeight: "500",
      }}
    >
      {copied ? (
        <span className="flex items-center gap-1.5">
          <svg className="w-3.5 h-3.5" fill="currentColor" viewBox="0 0 20 20">
            <path
              fillRule="evenodd"
              d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
              clipRule="evenodd"
            />
          </svg>
          <span className="hidden sm:inline">복사됨</span>
        </span>
      ) : (
        <span className="flex items-center gap-1.5">
          <svg
            className="w-3.5 h-3.5 group-hover:scale-110 transition-transform"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={1.5}
              d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z"
            />
          </svg>
          <span className="hidden sm:inline">복사</span>
        </span>
      )}
    </button>
  );
};

const components = {
  h2: (props) => {
    const { id, children, ...rest } = props;

    const handleLinkClick = (e) => {
      e.stopPropagation();
      if (id) {
        window.history.pushState(null, null, `#${id}`);
      }
    };

    return (
      <div className="group relative">
        <h2
          id={id}
          className="mt-12 text-3xl text-violet-900 font-semibold"
          {...rest}
        >
          {children}
        </h2>
        {id && (
          <button
            onClick={handleLinkClick}
            aria-label="헤딩 링크 복사"
            className="absolute left-0 top-1/2 -translate-x-8 -translate-y-1/2 opacity-0 group-hover:opacity-100 transition-opacity duration-200 p-1 rounded hover:bg-violet-100 cursor-pointer"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="text-violet-600 hover:text-violet-800"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1"
              />
            </svg>
          </button>
        )}
      </div>
    );
  },
  h3: (props) => {
    const { id, children, ...rest } = props;

    const handleLinkClick = (e) => {
      e.stopPropagation();
      if (id) {
        window.history.pushState(null, null, `#${id}`);
      }
    };

    return (
      <div className="group relative">
        <h3
          id={id}
          className="mt-7 text-2xl text-violet-900 font-semibold"
          {...rest}
        >
          {children}
        </h3>
        {id && (
          <button
            onClick={handleLinkClick}
            aria-label="헤딩 링크 복사"
            className="absolute left-0 top-1/2 -translate-x-8 -translate-y-1/2 opacity-0 group-hover:opacity-100 transition-opacity duration-200 p-1 rounded hover:bg-violet-100 cursor-pointer"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="text-violet-600 hover:text-violet-800"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1"
              />
            </svg>
          </button>
        )}
      </div>
    );
  },
  a: (props) => <a className="text-violet-900 hover:underline" {...props} />,
  p: (props) => <p className="mt-4 text-base leading-7" {...props} />,
  blockquote: (props) => (
    <blockquote
      className="mt-4 border-l-4 border-slate-200 pl-4 italic"
      {...props}
    />
  ),
  hr: (props) => <hr className="my-8 border-t border-slate-200" {...props} />,
  ul: (props) => (
    <ul className="pl-5 list-disc text-base leading-7" {...props} />
  ),
  ol: (props) => <ol className="mt-2 list-decimal list-inside" {...props} />,
  pre: (props) => {
    const { children, className } = props;

    let language = "text";
    let codeText = "";

    // 더 강력한 언어 추출 로직
    const extractLanguageAndCode = (element) => {
      if (React.isValidElement(element)) {
        // code 요소의 className에서 언어 추출
        if (element.props?.className) {
          const match = element.props.className.match(/language-(\w+)/);
          if (match) {
            return { language: match[1], found: true };
          }
        }

        // 재귀적으로 children 확인
        if (element.props?.children) {
          const childResult = extractLanguageAndCode(element.props.children);
          if (childResult.found) {
            return childResult;
          }
        }
      } else if (Array.isArray(element)) {
        for (const child of element) {
          const result = extractLanguageAndCode(child);
          if (result.found) {
            return result;
          }
        }
      }

      return { language: "text", found: false };
    };

    // 언어 추출 시도
    const result = extractLanguageAndCode(children);
    if (result.found) {
      language = result.language;
    }

    // className에서도 확인 (fallback)
    if (language === "text" && className) {
      const match = className.match(/language-(\w+)/);
      if (match) {
        language = match[1];
      }
    }

    // 코드 텍스트 추출
    const extractCode = (element) => {
      if (typeof element === "string") {
        return element;
      } else if (React.isValidElement(element)) {
        if (element.props?.children) {
          return extractCode(element.props.children);
        }
      } else if (Array.isArray(element)) {
        return element.map(extractCode).join("");
      }
      return "";
    };

    codeText = extractCode(children);

    const languageInfo = getLanguageInfo(language);

    return (
      <div className="my-8 rounded-lg overflow-hidden border border-gray-200 shadow-lg hover:shadow-xl transition-shadow duration-300">
        {/* 헤더 */}
        <div
          className="bg-gradient-to-r from-gray-50 to-gray-100 px-4 py-3 border-b border-gray-200"
          style={{
            background: "linear-gradient(to right, #f9fafb, #f3f4f6)",
            borderBottom: "1px solid #e5e7eb",
          }}
        >
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              {/* 터미널 스타일 버튼들 */}
              <div className="flex items-center gap-1.5">
                <div className="w-3 h-3 rounded-full bg-red-500 hover:bg-red-600 transition-colors cursor-pointer"></div>
                <div className="w-3 h-3 rounded-full bg-yellow-500 hover:bg-yellow-600 transition-colors cursor-pointer"></div>
                <div className="w-3 h-3 rounded-full bg-green-500 hover:bg-green-600 transition-colors cursor-pointer"></div>
              </div>
              {/* 언어 태그 */}
              <div
                className={`px-2.5 py-1 rounded-full text-xs font-medium border ${languageInfo.color}`}
              >
                <span>{languageInfo.name}</span>
              </div>
            </div>
            <CopyButton code={codeText} />
          </div>
        </div>

        {/* 코드 영역 */}
        <div className="relative" style={{ backgroundColor: "#fafbfc" }}>
          <pre
            className={`
              relative !p-6 !text-sm !leading-relaxed !overflow-x-auto !m-0 !border-0
              scrollbar-thin scrollbar-track-gray-100 scrollbar-thumb-gray-300
              ${className || ""}
            `}
            style={{
              padding: "1.5rem !important",
              margin: "0 !important",
              border: "none !important",
              backgroundColor: "#fafbfc !important",
              color: "#24292e !important",
              fontSize: "15px",
              lineHeight: "1.6",
              fontFamily:
                "'Fira Code', 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace",
            }}
            {...props}
          >
            {children}
          </pre>
        </div>
      </div>
    );
  },
  code: (props) => {
    const { children, className } = props;

    // 인라인 코드인지 코드 블록인지 확인
    const isInline =
      typeof children === "string" && !children.includes("\n") && !className;

    if (isInline) {
      // 인라인 코드 스타일
      return (
        <code
          style={{
            backgroundColor: "#f1f3f4",
            color: "#202124",
            border: "none",
            fontFamily:
              "'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace",
            fontSize: "0.85em",
            fontWeight: "400",
            padding: "0.1em 0.3em",
            borderRadius: "3px",
          }}
          {...props}
        />
      );
    }

    // 코드 블록 내부의 코드 (Prism.js가 처리)
    return <code className={className} {...props} />;
  },
};

const PostMDXProvider = ({ children }) => {
  useEffect(() => {
    // 컴포넌트가 마운트될 때 Prism.js로 하이라이팅 적용
    Prism.highlightAll();
  }, []);

  return <MDXProvider components={components}>{children}</MDXProvider>;
};

export default PostMDXProvider;
